/* Basic colors */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

.container {
  max-width: 90%;
  margin: 0 auto;
}
.container h2 {
  color: #326dee;
}
.container a {
  color: #326dee;
}
.container .error {
  color: #d32752;
  border: 2px solid #d32752;
  width: 200px;
  height: 40px;
}
.container .notify {
  color: #f6c31c;
  border: 2px solid #1ac888;
  padding: 0.75rem;
  width: 200px;
  height: 40px;
}
.container button {
  border: none;
  color: #ffffff;
  background: #326dee;
  padding: 0.75rem;
  margin: 0.75rem;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
}